import React, { useState } from 'react';
import {Panel, PanelHeader, Group, Div, HeaderButton } from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './style.css';

const Shop = props => {	
	
	const [items, setItems] = useState(JSON.parse(localStorage.getItem('items')));
	const [prices, setPrices] = useState(JSON.parse(localStorage.getItem('prices')));
	const [data, setData] = useState(JSON.parse(localStorage.getItem('data')));


	// Покупка предмета в магазине
	const buy = ( item ) => {
		if(props.score - prices[item].price >= 0) {
			props.add(-prices[item].price);

			// Установка состояния компонента
			setItems(Object.assign(items,{ [item]: items[item] + 1 }));
			setPrices(Object.assign(prices, { [item]: Object.assign(prices[item], { price: Math.round(prices[item].price * 1.1)})}));
			if(prices[item].boost) {
				setData(Object.assign(data,{ auto: data.auto + prices[item].boost }));
			}
			if(prices[item].perClick) {
				setData(Object.assign(data,{ perClick: data.perClick + prices[item].perClick }));
			}
			// Установка хранилища LocalStorage
			setStorage('items',items);
			setStorage('prices',prices);
			setStorage('data',data);
		}
	}

	// Установка хранилища LocalStorage
	const setStorage = ( name, obj ) => {
		localStorage.setItem(`${name}`, JSON.stringify(obj));
	}

	// Локализация магазина
	const locale = {
		phone: 'Телефон',
		notebook: 'Ноутбук',
		pc: 'Компьютер',
		office: 'Офис',
		farm: 'Ферма',
		factory: 'Завод',
		mine: 'Шахта',
		skyscrapper: 'Небоскрёб',
		island: 'Остров',
		country: 'Страна',
		planet: 'Планета',
		solarSystem: 'Солнечная система',
		cosmos: 'Космос'
	}

	return(
		<Panel id={props.id}>
			<PanelHeader theme="sekki" left={<HeaderButton><Icon24Back onClick={props.go} data-to='default' /> </HeaderButton> }>
				Магазин
			</PanelHeader>
			<Group>
				<Div className="shop-score">
					{Math.round(props.score)}
				</Div>
			</Group>
			{Object.keys(items).map((item) => (

						<Group key={item}>
							<Div onClick={() => buy(item)} className="shop-item">
								<h4>{locale[item]}</h4> <p>Цена: { prices[item].price } | Кол-во: {items[item]} | +{ prices[item].boost }/с | {prices[item].perClick ? prices[item].perClick + '/ за клик' : null}</p>             
							</Div>
						</Group>
					))
			}
		</Panel>
	);
}

export default Shop;
