import React, { useState, useEffect } from 'react'
import {Panel, PanelHeader} from '@vkontakte/vkui';
import { Group, Div, Button, InfoRow, Progress } from '@vkontakte/vkui';
import './style.css';
import { createGrid } from '../../core/main';

const Default = props => {

    const data = props.data;

    const add = () => {
        props.add(data.perClick);
    }

    const changeGrid = ( item ) => {
        if(props.grid[item].clickAble === true) {
            props.grid[item].clicked = !props.grid[item].clicked;
        }

        if(props.grid[item].clickAble !== true) {
            props.grid.map((item) => {
                if(item.clicked === true) {
                    item.clicked = false;
                }
            });
        }

        if(props.grid[item].clickAble === false) {
            props.setLVL(1);
        }
        props.setGrid(props.grid);
    }

    return (
      <Panel id={props.id} theme="white">
        <PanelHeader>
            Кликер(0.0.6)
		</PanelHeader>
        <Div className="button-block">
            <button stretched="true" onClick={props.go} data-to="shop" style={{ marginRight: 8 }}>
                Магазин
            </button>
            <button stretched="true" disabled onClick={props.go} data-to="upgrades" style={{ marginRight: 8 }}>
                Улучшения
            </button>
            <button stretched="true" onClick={props.go} data-to="settings">
                Настройки
            </button>
        </Div>
        <Div>
            <Div className="score-container">
                <p>{Math.round(props.score)} #</p><p className={props.bonusTime >= 9 ? 'reward show' : 'reward'}>+{props.lvl * 2 * data.perClick}</p>
                <p>{ props.lvl }LVL</p>
                <p>В сек. { data.auto } | За клик: { data.perClick}</p>
            </Div>
            <Div>
                <InfoRow title={props.bonusTime}>
                    <Progress value={props.bonusTime * 10} />
                </InfoRow>
            </Div>         
            <Div className="button-container">
                {Object.keys(props.grid).map((item) => (
                    <button id={item} key={item} className={ props.grid[item].clicked === true ? "button-click green" : "button-click"} onClick={(e) => changeGrid(e.target.id)}>
                    { props.grid[item].clickAble === true ? '+' : null }
                </button>
                ))
                }
            </Div>
        </Div>
      </Panel>
    )
}
  
  export default Default
