import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Shop from './panels/Shop';
import Default from './panels/Default';
import Settings from './panels/Settings/';
import { firstStart, reset, createGrid } from './core/main';
import './style.css';

firstStart();

class App extends React.Component {
	constructor(props) {
		super(props);
		this.auto = undefined;
		this.state = {
			activePanel: 'default',
			fetchedUser: null,
			score: parseInt(localStorage.getItem('score')),
			data: JSON.parse(localStorage.getItem('data')),
			timer: false,
			speed: 1,
			grid: createGrid(),
			lvl: 1,
			bonusTime: 0
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
			this.tick();
	}

	tick = () => {
		this.auto = setInterval(() => {
			this.add(this.state.data.auto / this.state.speed);
			if(this.state.lvl >= 2) {
				this.setState({ bonusTime: this.state.bonusTime === 0 ? this.state.bonusTime : this.state.bonusTime - 1 });
				if(this.state.bonusTime === 0) {
					this.setState({ lvl: 1 });
				}
			}

			if(this.state.lvl === 1) {
				this.setState({ bonusTime: 0 });
			}
		},1000 / this.state.speed);
	}

	componentWillUnmount() {
		return function cleanup() {
			clearInterval(this.auto);
		};
	}

	add = ( additional ) => {
    if(typeof score !== 'object') {
				localStorage.setItem('score', this.state.score + additional);
				this.setState({ data: JSON.parse(localStorage.getItem('data')), score: this.state.score + additional });
    }
	}

	setGrid = ( grid ) => {
		let sum = 0;
		this.setState({ grid: grid });
		 grid.map((item) => {
			if(item.clickAble === true && item.clicked === true) {
				sum= sum + 1;
			} 
		}); 
		if(sum === 3) {
			setTimeout(() => {
				this.reGrid();
			}, 200);
			
		}
	}

	reGrid = () => {
		this.setState({ grid: createGrid(), lvl: this.state.lvl === 20 ? this.state.lvl : this.state.lvl + 1, bonusTime: 10 });
		this.add( this.state.lvl * 2 * this.state.data.perClick );
	}

	setLVL = ( lvl ) => {
		this.setState({ lvl: lvl });
	}
	
	//fixed bug with additional score from data object after reset
	fromScratch = () => {
		clearInterval(this.auto);
		reset();
		this.setState({ score: 0, data: { auto: 0, perClick: 1 }, bonusTime: 0, lvl: 0 });
		this.tick();
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Shop id="shop" add={this.add} score={this.state.score} go={this.go} />
				<Default id="default" bonusTime={this.state.bonusTime} add={this.add} setLVL={this.setLVL} lvl={this.state.lvl} setGrid={this.setGrid} grid={this.state.grid} data={this.state.data} score={this.state.score} go={this.go} />
				<Settings id="settings" add={this.add} reset={this.fromScratch} score={this.state.score} go={this.go} />
			</View>
		);
	}
}

export default App;
